<template>
  <v-dialog v-model="showDialog">
    <v-card style="height:90vh;width:100%">
      <v-card-title class="justify-center">{{ title }}</v-card-title>
      <v-card-text style="height:90%;width:100%;">
        <embed id="fileViewer" style="height:100%;width:100%">
      </v-card-text>
      <v-card-actions class="text-xs-center">
        <v-flex text-xs-center>
          <v-btn color="success" @click="showDialog=false">Close</v-btn>
        </v-flex>
      </v-card-actions>
    </v-card>
  </v-dialog>
</template>

<script>
export default {
  name: "fileViewerDialog",
  data: function() {
    return {
      showDialog: false,
      title: null,
      buffer: null
    };
  },
  methods: {
    show: function(title, file) {
      this.title = title;
      document.getElementById("fileViewer").src = file; // works better
      this.showDialog = true;
    }
  }
};
</script>

<style>
.v-dialog {
  overflow-y: hidden;
}
.v-dialog .v-card__title,
.v-dialog .v-card__text,
.v-dialog .v-card__actions {
  padding-top: 5px;
  padding-bottom: 5px;
}
</style>