<template>
  <div>
    <v-container v-if="showPhysicianInfo">
      <custom-snackbar ref="customSnackbar"></custom-snackbar>
      <v-card>
        <v-card-title class="headline font-weight-regular blue-grey white--text">Provider</v-card-title>
        <v-card-text>
          <v-form ref="form">
            <v-container fluid>
              <v-subheader>Audit Details</v-subheader>
              <v-divider></v-divider>
              <v-layout row wrap>
                <v-flex xs12 md4>
                  <v-text-field v-model="audit[0].id" label="Audit ID" disabled></v-text-field>
                </v-flex>
                <v-flex xs12 md4>
                  <v-text-field v-model="auditorName" label="Auditor" disabled></v-text-field>
                </v-flex>
                <v-flex xs12 md4>
                  <v-text-field v-model="audit[0].audit_date" label="Audit Date" disabled></v-text-field>
                </v-flex>
              </v-layout>
              <v-layout row wrap>
                <v-flex xs12 md4 style="display: flex;">
                  <v-text-field v-model="audit[0].provider_name" label="Provider Name" disabled></v-text-field>
                  <a
                    v-if="audit[0].provider_email"
                    :href="`mailto:${audit[0].provider_email}`"
                    style="margin:auto;"
                  >
                    <i class="material-icons">email</i>
                  </a>
                </v-flex>
                <v-flex xs12 md2>
                  <v-text-field v-model="audit[0].npi" label="Provider NPI" disabled></v-text-field>
                </v-flex>
                <v-flex xs12 md2>
                  <v-text-field v-model="audit[0].entity_name" label="Facility" disabled></v-text-field>
                </v-flex>
                <v-flex xs12 md4>
                  <v-select v-model="audit[0].audit_type" :items="auditTypes" label="Audit Type"></v-select>
                </v-flex>
              </v-layout>
              <v-subheader>Audit Results</v-subheader>
              <v-divider></v-divider>
              <v-layout row wrap>
                <v-flex xs12 md6>
                  <v-checkbox v-model="audit[0].no_charges_found" label="No Charges Found"></v-checkbox>
                </v-flex>
                <v-flex xs12 md6>
                  <v-checkbox
                    v-model="audit[0].manually_selected_charges"
                    label="Manually Selected Charges"
                  ></v-checkbox>
                </v-flex>
              </v-layout>
              <v-layout row wrap>
                <v-flex xs12 md6>
                  <v-menu
                    ref="menuDatePickerChargeCorrection"
                    :close-on-content-click="false"
                    v-model="menuDatePickerChargeCorrection"
                    :nudge-right="40"
                    :return-value.sync="audit[0].charge_correction_start_date"
                    lazy
                    transition="scale-transition"
                    offset-y
                    full-width
                    min-width="290px"
                  >
                    <v-text-field
                      slot="activator"
                      v-model="audit[0].charge_correction_start_date"
                      label="Charge Correction Start Date"
                      prepend-icon="event"
                      readonly
                      data-vv-name="Charge Correction Start Date"
                      :error-messages="errors.collect('Charge Correction Start Date')"
                    ></v-text-field>
                    <v-date-picker
                      v-model="audit[0].charge_correction_start_date"
                      @input="$refs.menuDatePickerChargeCorrection.save(audit[0].charge_correction_start_date)"
                    ></v-date-picker>
                  </v-menu>
                </v-flex>
                <v-flex xs12 md6>
                  <v-layout>
                    <v-flex xs12 md4 pr-1>
                      <v-text-field
                        v-model="audit[0].education_type"
                        label="Communication Type"
                        disabled
                      ></v-text-field>
                    </v-flex>
                    <v-flex xs12 md5 pr-1>
                      <v-text-field
                        v-model="audit[0].education_date"
                        label="Results Communicated Date"
                        disabled
                      ></v-text-field>
                    </v-flex>
                    <v-flex xs12 md3>
                      <v-btn @click="showEducation()">Education ></v-btn>
                    </v-flex>
                  </v-layout>
                </v-flex>
              </v-layout>
              <v-layout row wrap>
                <v-flex xs12 md6>
                  <v-menu
                    ref="menuDatePickerAuditComplete"
                    :close-on-content-click="false"
                    v-model="menuDatePickerAuditComplete"
                    :nudge-right="40"
                    :return-value.sync="audit[0].audit_completed_date"
                    lazy
                    transition="scale-transition"
                    offset-y
                    full-width
                    min-width="290px"
                  >
                    <v-text-field
                      slot="activator"
                      v-model="audit[0].audit_completed_date"
                      label="Audit Completed Date"
                      prepend-icon="event"
                      readonly
                      data-vv-name="Audit Completed Date"
                      :error-messages="errors.collect('Audit Completed Date')"
                    ></v-text-field>
                    <v-date-picker
                      v-model="audit[0].audit_completed_date"
                      @input="$refs.menuDatePickerAuditComplete.save(audit[0].audit_completed_date)"
                    ></v-date-picker>
                  </v-menu>
                </v-flex>
                <v-flex xs12 md6>
                  <v-text-field v-model="auditScore" label="Audit Score" disabled></v-text-field>
                </v-flex>
              </v-layout>
              <v-layout row wrap>
                <v-flex>
                  <v-btn @click="saveAudit()">Save</v-btn>
                  <v-btn @click="cancelAudit()">Cancel</v-btn>
                  <v-btn v-if="currentUser.role =='administrator'" @click="deleteAudit()">Delete</v-btn>
                </v-flex>
              </v-layout>
              <v-subheader>
                Encounters
                <v-tooltip bottom>
                  <i
                    class="material-icons"
                    @click="addEncounter()"
                    slot="activator"
                    style="color:#000000"
                  >add_box</i>
                  <span>Create Additional Encounters</span>
                </v-tooltip>
              </v-subheader>
              <v-divider></v-divider>
              <v-layout row wrap>
                <v-flex xs12>
                  <v-list>
                    <v-list-tile-title>
                      <v-layout>
                        <v-flex md3>Patient Name</v-flex>
                        <v-flex md3>CPT</v-flex>
                        <v-flex md2>Date of Service</v-flex>
                        <v-flex md2>Status</v-flex>
                        <v-flex md2></v-flex>
                      </v-layout>
                    </v-list-tile-title>
                    <template v-for="(encounter, index) in audit">
                      <v-list-tile v-if="encounter.patient_name" :key="index">
                        <v-layout>
                          <v-flex md3>{{ encounter.patient_name }}</v-flex>
                          <v-flex md3>{{ encounter.cpt_billed }}</v-flex>
                          <v-flex md2>{{ encounter.service_date }}</v-flex>
                          <v-flex md2 v-if="encounter.audit_completion">Complete</v-flex>
                          <v-flex md2 v-else>Pending</v-flex>
                          <v-flex md2>
                            <v-tooltip bottom>
                              <i
                                class="material-icons"
                                @click="auditEncounter(encounter.audit_record_id)"
                                slot="activator"
                              >edit</i>
                              <span>View/Edit Encounter</span>
                            </v-tooltip>
                            <v-tooltip bottom>
                              <i
                                class="material-icons"
                                @click="deleteEncounter(encounter.audit_record_id, index)"
                                slot="activator"
                              >delete</i>
                              <span>Delete Encounter</span>
                            </v-tooltip>
                          </v-flex>
                        </v-layout>
                      </v-list-tile>
                    </template>
                    <v-list-tile v-if="!audit[0].patient_name">
                      <v-layout>
                        <v-flex md12>None</v-flex>
                      </v-layout>
                    </v-list-tile>
                  </v-list>
                </v-flex>
              </v-layout>
            </v-container>
          </v-form>
        </v-card-text>
      </v-card>
      <patient-selection
        id="patientSelectionBlock"
        ref="PatientSelection"
        style="margin-top:10px;"
        v-on:created="init()"
      ></patient-selection>
      <patient-selection-manual
        id="patientSelectionManualBlock"
        ref="PatientSelectionManual"
        style="margin-top:10px;"
      ></patient-selection-manual>
    </v-container>
    <education-dialog
      v-if="showEducationDialog"
      :inPhysicianAuditId="audit[0].id"
      :inPhysicianNpi="audit[0].npi"
      @interfaceClose="closeEducation"
    ></education-dialog>
    <confirmation-dialog
      ref="confirmationDialog"
      :title="confirmation.title"
      :message="confirmation.message"
    ></confirmation-dialog>
  </div>
</template>

<script>
import Config from "../config";
import ConfirmationDialog from "@/components/ConfirmationDialog";
import CustomSnackbar from "@/components/Snackbar";
import PatientSelection from "@/components/PatientSelection";
import EducationDialog from "@/components/EducationDialog";
import PatientSelectionManual from "@/components/PatientSelectionManual";
//import draggable from "vuedraggable";
import httpService from "../httpService";
import Router from "../router";

// audit_date: new Date().toJSON()
export default {
  name: "Main",
  components: {
    EducationDialog,
    ConfirmationDialog,
    CustomSnackbar,
    PatientSelection,
    PatientSelectionManual
  },
  data: () => ({
    showPhysicianInfo: true,
    showEducationDialog: false,
    menuDatePickerChargeCorrection: null,
    menuDatePickerAuditComplete: null,
    audit: [
      {
        id: null,
        audit_provider_id: null,
        npi: null,
        provider_name: null,
        entity_name: null,
        audit_date: null,
        charge_correction_start_date: null,
        audit_completed_date: null,
        no_charges_found: null,
        manually_selected_charges: null,
        audit_record_id: null,
        patient_name: null,
        service_date: null,
        education_audit_id: null,
        education_type: null,
        audit_type: null,
        education_date: null,
        cpt_billed: null,
        encounter_status: null,
        encounter_score: null
      }
    ],
    confirmation: {
      title: "",
      message: ""
    }
  }),
  created() {
    // work on this one record
    //this.audit = this.audits.find(elem => elem.audit_id == this.$route.params.audit_id);
    this.init();
  },
  computed: {
    newRecord() {
      return false;
    },
    currentUser() {
      return this.$store.getters.CURRENT_USER;
    },
    auditors() {
      return this.$store.getters.ALL_USERS;
    },
    auditorName() {
      if (this.audit[0].auditor_id >= 0) {
        const indx = this.auditors.findIndex(
          obj => obj.id === this.audit[0].auditor_id
        );
        return this.auditors[indx].fullname;
      }
    },
    physicianAuditScore_notused() {
      return this.audit.reduce((acc, val) => {
        return isNaN(val.audit_score) ? acc : acc + val.audit_score;
      }, 0);
    },
    auditTypes() {
      return this.$store.getters.APPLICATION_LISTS.filter(
        row => row.keyword === "AUDIT_TYPE"
      ).map(row => row.value);
    },
    auditScore() {
      let rows = 0;
      let score = 0;

      for (let i = 0; i < this.audit.length; i++) {
        if (this.audit[i].audit_score) {
          score += this.audit[i].audit_score;
        }
        rows++;
      }
      return ((10 * rows - score) / (10 * rows)) * 100;
    }
  },
  methods: {
    async init() {
      console.log("initializing..");
      await httpService.get(
        `${Config.apiRoot}/audit/physician/audit/${this.$route.params.npi}/${this.$route.params.provider_audit_id}`,
        (status, data) => {
          this.audit = data.message;
        }
      );
    },
    auditEncounter: function(id) {
      // user selected a record to work on
      Router.push({ name: "audit-record", params: { audit_id: id } });
    },
    saveAudit: function() {
      // Always update the 1st record for the physician audit data
      this.$validator.validateAll().then(async result => {
        if (!result) {
          return;
        } else {
          // this.audit[0].audit_type = "cancelled"; // WADE not sure why this is here
          httpService.put(
            `${Config.apiRoot}/audit/physician/audit/${this.audit[0].id}`,
            this.audit[0],
            (status, data) => {
              if (data.success) {
                this.showMessage("success", "Record sucessfully saved");
              } else {
                this.showMessage(
                  "error",
                  "There was a problem saving your changes.  MSG - " +
                    data.message.originalError.info.message
                );
              }
            }
          );
        }
      });
    },
    cancelAudit: function() {
      this.confirmation.title = "Cancel Audit?";
      this.confirmation.message =
        "Are you sure you want to cancel your changes?";
      this.$refs.confirmationDialog.confirmation().then(res => {
        if (res) {
          this.$router.go(-1);
        }
      });
    },
    deleteAudit: function() {
      this.confirmation.title = "Delete Provider's Audit?";
      this.confirmation.message =
        "Are you sure you want to delete this provider's audit?";
      this.$refs.confirmationDialog.confirmation().then(res => {
        if (res) {
          // set status of audit to delete
          this.audit[0].audit_type = "cancelled";
          httpService.put(
            `${Config.apiRoot}/audit/physician/audit/${this.audit[0].id}`,
            this.audit[0],
            (status, data) => {
              if (data.success) {
                this.showMessage("success", "Record sucessfully saved");
                this.$router.go(-1);
              } else {
                this.showMessage(
                  "error",
                  "There was a problem saving your changes.  MSG - " +
                    data.message.originalError.info.message
                );
              }
            }
          );
        }
      });
    },
    popEncounter: function(index) {
      // remove item from the display list
      this.audit.splice(index, 1);
    },
    addEncounter: function() {
      // WADE - manual charges
      this.saveAudit();
      if (this.audit[0].manually_selected_charges) {
        // user selected a record to work on
        // this.$refs.PatientSelectionManual.showComponent(
        //   this.audit[0].id,
        //   this.audit[0].npi
        // );
        // // scroll to the new object
        // document
        //   .getElementById("patientSelectionManualBlock")
        //   .scrollIntoView(false);

        Router.push({
          name: "audit-record",
          params: {
            audit_id: -1,
            npi: this.audit[0].npi,
            provider_audit_id: this.$route.params.provider_audit_id
          }
        });
      } else {
        // user selected a record to work on
        this.$refs.PatientSelection.showComponent(
          this.audit[0].id,
          this.audit[0].npi
        );
        // scroll to the new object
        document.getElementById("patientSelectionBlock").scrollIntoView(false);
      }
    },
    deleteEncounter: function(id, index) {
      this.confirmation.title = "Delete Encounter?";
      this.confirmation.message =
        "Are you sure you want to delete this encounter?";
      this.$refs.confirmationDialog.confirmation().then(res => {
        if (res) {
          httpService.delete(
            `${Config.apiRoot}/audit/${id}`,
            (status, data) => {
              if (data.success) {
                this.showMessage("success", "Encounter sucessfully deleted");
                this.popEncounter(index);
              } else {
                this.showMessage(
                  "error",
                  "There was a problem deleting the encounter.  MSG - " +
                    data.message.originalError.info.message
                );
              }
            }
          );
        }
      });
    },
    showMessage: function(type, message) {
      this.$refs.customSnackbar.showSnackbar(message, type, 3000, true);
    },
    showEducation: function(id) {
      this.showPhysicianInfo = false;
      this.showEducationDialog = true;
    },
    closeEducation: function(show, educationDate) {
      this.showEducationDialog = false;
      this.showPhysicianInfo = true;
      if (educationDate) {
        this.audit[0].education_date = educationDate;
      }
    }
  }
};
</script>

<style>
i {
  padding: 0 10px;
}
i:hover {
  cursor: pointer;
}
</style>
