<template>
  <div>
    <v-container>
      <v-layout row wrap v-if="currentUser.role === 'administrator'">
        <v-flex xs12 md4 offset-md8 class="pr-1" right>
          <v-select
            v-model="auditorId"
            :items="auditors"
            item-text="fullname"
            item-value="id"
            box
            label="Auditors"
          ></v-select>
        </v-flex>
      </v-layout>
      <v-layout row wrap style="margin-bottom: 25px;">
        <v-flex xs12 md6 class="pr-1">
          <!-- TODO - Progress Tracker -->
          <v-layout row wrap>
            <v-flex md3 class="pr-1 border"></v-flex>
            <v-flex md3 class="pr-1 border">Projected</v-flex>
            <v-flex md3 class="pr-1 border">Completed</v-flex>
            <v-flex md3 class="pr-1 border">Remaining</v-flex>
          </v-layout>
          <v-layout row wrap>
            <v-flex md3 class="pr-1 border">Providers</v-flex>
            <v-flex md3 class="pr-1 border">{{ totals.provider_projected }}</v-flex>
            <v-flex md3 class="pr-1 border">{{ totals.provider_completed }}</v-flex>
            <v-flex md3 class="pr-1 border">{{ totals.provider_remaining }}</v-flex>
          </v-layout>
          <v-layout row wrap>
            <v-flex md3 class="pr-1 border">Encounters</v-flex>
            <v-flex md3 class="pr-1 border">{{ totals.encounter_projected }}</v-flex>
            <v-flex md3 class="pr-1 border">{{ totals.encounter_completed }}</v-flex>
            <v-flex md3 class="pr-1 border">{{ totals.encounter_remaining }}</v-flex>
          </v-layout>
          <v-layout row wrap>
            <v-flex md3 class="pr-1 border">Educations</v-flex>
            <v-flex md3 class="pr-1 border">{{ totals.educations_projected }}</v-flex>
            <v-flex md3 class="pr-1 border">{{ totals.educations_completed }}</v-flex>
            <v-flex md3 class="pr-1 border">{{ totals.educations_remaining }}</v-flex>
          </v-layout>
        </v-flex>
        <v-flex xs12 md6 class="pr-1">
          <!-- TODO - Notifications -->
          <v-layout row wrap>
            <v-flex md6 offset-md3 class="pr-1 border">New Assignments [{{ newAssignmentsCount }}]</v-flex>
          </v-layout>
          <v-layout row wrap>
            <v-flex
              md6
              offset-md3
              class="pr-1 border"
            >Terminated Providers [{{ terminated.length }}]</v-flex>
          </v-layout>
          <v-layout row wrap>
            <v-flex md6 offset-md3 class="pr-1 border">High Risk [{{ highRiskScoreCount }}]</v-flex>
          </v-layout>
          <!-- <v-layout row wrap>
            <v-flex md6 offset-md3 class="pr-1 border">Require Re-Audit []</v-flex>
          </v-layout>-->
        </v-flex>
      </v-layout>

      <v-layout row wrap>
        <v-flex xs12 md12 class="pr-1">
          <v-card class="mb-2">
            <v-card-title
              class="headline font-weight-regular blue-grey white--text"
            >Audit Assignments</v-card-title>
            <v-card-text>
              <v-data-table
                :key="key"
                :headers="tableHeaders"
                :items="audits"
                class="evelation-1"
                :pagination.sync="pagination"
              >
                <template slot="items" slot-scope="{ item }">
                  <tr
                    @click="auditPhysician(item.npi, item.provider_audit_id)"
                    style="cursor:pointer;"
                  >
                    <td>{{ item.status }}</td>
                    <td>{{ item.provider_name }}</td>
                    <td>{{ item.entity_name }} - {{ item.group_practice }}</td>
                    <td>{{ item.audit_date }}</td>
                    <td>{{ item.med_system }}</td>
                    <td>{{ item.billing_system }}</td>
                    <td>{{ item.audit_type }}</td>
                    <td>{{ item.in_progress }}</td>
                    <td>{{ item.complete }}</td>
                    <td>
                      <v-tooltip bottom v-if="currentUser.role === 'administrator'">
                        <i
                          class="material-icons"
                          @click="deleteAudit(item.provider_audit_id, $event)"
                          slot="activator"
                        >delete</i>
                        <span>Delete Audit and All Associated Encounters</span>
                      </v-tooltip>
                    </td>
                  </tr>
                </template>
              </v-data-table>
            </v-card-text>
          </v-card>
        </v-flex>
      </v-layout>
    </v-container>
    <confirmation-dialog
      ref="confirmationDialog"
      :title="confirmation.title"
      :message="confirmation.message"
    ></confirmation-dialog>
  </div>
</template>

<script>
import Config from "../config";
import Router from "../router";
import httpService from "../httpService";
import ConfirmationDialog from "@/components/ConfirmationDialog";

export default {
  name: "Main",
  components: { ConfirmationDialog },
  data: () => ({
    audits: [],
    terminated: [],
    auditorId: null,
    key: 0,
    totals: {
      provider_projected: 0,
      provider_completed: 0,
      provider_remaining: 0,
      encounter_projected: 0,
      encounter_completed: 0,
      encounter_remaining: 0,
      educations_projected: 0,
      educations_completed: 0,
      educations_remaining: 0
    },
    tableHeaders: [
      { text: "Status", value: "status" },
      { text: "Provider", value: "provider_name" },
      { text: "Practice", value: "practice" },
      { text: "Audit Date", value: "audit_date" },
      { text: "Med System", value: "med_system" },
      { text: "Billing System", value: "billing_system" },
      { text: "Audit Type", value: "audit_type" },
      { text: "In Progress", value: "in_progress" },
      { text: "Completed", value: "complete" },
      { text: "", value: "adminDelete" }
    ],
    pagination: {
      rowsPerPage: 10
    },
    confirmation: {
      title: "",
      message: ""
    }
  }),
  async mounted() {
    console.log(
      "this.$store.getters.CURRENT_USER - ",
      this.$store.getters.CURRENT_USER
    );
    this.auditorId = this.currentUser.id; // when are computed items ready?
    this.getAudits();
  },
  watch: {
    auditorId: function(id) {
      this.getAudits();
    }
  },
  computed: {
    auditors() {
      return this.$store.getters.ALL_USERS;
    },
    currentUser() {
      return this.$store.getters.CURRENT_USER;
    },
    physicians() {
      return this.$store.getters.PHYSICIAN;
    },
    physicianLookup() {
      return this.physicians.map(row => {
        let res = {};
        res.name = row.last_name + ", " + row.first_name;
        res.npi = row.npi;
        return res;
      });
    },
    highRiskScoreCount() {
      let kntr = 0;
      this.audits.map(row => {
        if (row.last_audit_score != -1 && row.last_audit_score <= 75) {
          kntr++;
        }
      });
      return kntr;
    },
    newAssignmentsCount() {
      let kntr = 0;
      this.audits.map(row => {
        if (row.education_completed_count == 0) {
          kntr++;
        }
      });
      return kntr;
    }
  },
  methods: {
    getAudits() {
      httpService.get(
        `${Config.apiRoot}/audit/dashboard2/${this.auditorId}`,
        (status, data) => {
          this.audits = data.message;
          this.terminated = data.message2;

          //preset 2 fields for sorting
          for (let i = 0; i < this.audits.length; i++) {
            this.audits[i].status = this.getStatus(this.audits[i]);
            this.audits[i].in_progress = this.calcIncomplete(this.audits[i]);
          }
          this.computeTotals();
          this.key++;
        }
      );
    },
    computeTotals() {
      this.totals.provider_projected = 0;
      this.totals.provider_completed = 0;
      this.totals.provider_remaining = 0;
      this.totals.encounter_projected = 0;
      this.totals.encounter_completed = 0;
      this.totals.encounter_remaining = 0;
      this.totals.educations_projected = 0;
      this.totals.educations_completed = 0;
      this.totals.educations_remaining = 0;

      for (let i = 0; i < this.audits.length; i++) {
        this.totals.provider_projected++; // one for each provider in list
        if (this.audits[i].status == "Completed") {
          this.totals.provider_completed++;
        }
        this.totals.provider_remaining =
          this.totals.provider_projected - this.totals.provider_completed;
        this.totals.encounter_projected +=
          this.audits[i].in_progress + this.audits[i].complete;
        this.totals.encounter_completed += this.audits[i].complete;
        this.totals.encounter_remaining += this.audits[i].in_progress;
        this.totals.educations_projected++; // one for each provider in list
        this.totals.educations_completed += this.audits[
          i
        ].education_completed_count;
        this.totals.educations_remaining =
          this.totals.educations_projected - this.totals.educations_completed;
      }
      this.totals;
    },
    deleteAudit(auditId, event) {
      console.log("auditId", auditId);

      event.stopPropagation();

      this.confirmation.title = "Delete Audit?";
      this.confirmation.message = "Are you sure you want to delete this audit?";
      this.$refs.confirmationDialog.confirmation().then(res => {
        if (res) {
          httpService.delete(
            `${Config.apiRoot}/audit/provider/${auditId}`,
            (status, data) => {
              if (data.success) {
                //                this.showMessage("success", "Audit sucessfully deleted");
                this.popAudit(auditId);
              } else {
                this.showMessage(
                  "error",
                  "There was a problem deleting the audit.  MSG - " +
                    data.message.originalError.info.message
                );
              }
            }
          );
        }
      });
    },
    calcIncomplete(provider) {
      // incomplete returns 1 if there are no joined rows, check then adjust
      if (provider.count_of_linked_rows === 0) {
        return 0;
      }
      return provider.incomplete;
    },
    getStatus(provider) {
      if (provider.audit_completed_date) {
        return "Completed";
      }
      return "In Progress";
    },
    auditPhysician(npi, provider_audit_id) {
      // user selected a record to work on
      Router.push({
        name: "audit-provider",
        params: { npi: npi, provider_audit_id: provider_audit_id }
      });
    },
    auditPatient(id) {
      // not used
      // user selected a record to work on
      Router.push({ name: "audit-record", params: { audit_id: id } });
    },
    popAudit: function(id) {
      // remove item from the display list based on audit id
      this.audits.splice(
        this.audits.findIndex(item => item.provider_audit_id == id),
        1
      );
    }
  }
};
</script>

<style>
.v-datatable th,
.v-datatable td {
  padding-left: 5px !important;
  padding-right: 5px !important;
}
/* .border {
  border: solid;
} */
</style>