<template>
  <div>
    <v-container>
      <custom-snackbar ref="customSnackbar"></custom-snackbar>
      <v-card class="mb-2">
        <v-card-title class="headline font-weight-regular blue-grey white--text">Provider Education</v-card-title>
        <v-card-text>
          <v-subheader class="pa-0">Provider Lookup</v-subheader>
          <v-autocomplete
            v-model="selectedNpi"
            :items="physicianLookup"
            persistent-hint
            item-text="name"
            item-value="npi"
            @change="selectPhysician(selectedNpi)"
          ></v-autocomplete>
        </v-card-text>
      </v-card>
      <v-card v-if="showPhysicianProfile && !showEducationDialog">
        <v-card-title class="headline font-weight-regular blue-grey white--text">
          Education
          <i
            class="material-icons"
            @click="showEducation()"
            style="padding-left:8px;"
          >add_box</i>
        </v-card-title>
        <v-data-table
          :headers="tableHeaders"
          :items="physicianEducation"
          class="evelation-1"
          :pagination.sync="pagination"
        >
          <template slot="items" slot-scope="{ item }">
            <tr
              @click="educationSelection(item.type, item.physician_audit_id, item.id)"
              style="cursor:pointer;"
            >
              <td>{{ item.physician_audit_id }}</td>
              <td>{{ item.type }}</td>
              <td>{{ item.method }}</td>
              <td>{{ item.education_date }}</td>
            </tr>
          </template>
        </v-data-table>
      </v-card>
      <file-viewer ref="fileViewerDialog" title="test" buffer="null"></file-viewer>
      <!--
      <v-dialog v-model="showEducationBlob">
        <v-card style="height:100%;width:100%">
          <v-card-title id="fileViewerTitle" class="justify-center">Dialog 2</v-card-title>
          <v-card-text style="height:90%;width:100%">
            <embed id="fileViewer" style="height:100%;width:100%">
          </v-card-text>
          <v-card-actions class="text-xs-center">
            <v-flex text-xs-center>
              <v-btn color="success" @click="showEducationBlob=false">Close</v-btn>
            </v-flex>
          </v-card-actions>
        </v-card>
      </v-dialog>
      -->
      <education-dialog
        v-if="showEducationDialog"
        :inPhysicianAuditId="educationIndex"
        @interfaceClose="closeEducation"
      ></education-dialog>
    </v-container>
  </div>
</template>

<script>
import Config from "../config";
import EducationDialog from "@/components/EducationDialog";
import FileViewer from "@/components/FileViewer";
import CustomSnackbar from "@/components/Snackbar";
import httpService from "../httpService";

export default {
  name: "Main",
  components: {
    EducationDialog,
    CustomSnackbar,
    FileViewer
  },
  data: () => ({
    menuDatePicker: null,
    selectedNpi: null,
    showPhysicianProfile: false,
    currentPhysician: null,
    physicianEducation: null,
    showEducationDialog: false,
    fileViewerDialog: false,
    tableHeaders: [
      { text: "Audit ID", value: "physician_audit_id" },
      { text: "Type", value: "type" },
      { text: "Method", value: "method" },
      { text: "Date", value: "date" }
    ],
    pagination: {
      rowsPerPage: 10
    },
    educationIndex: null
  }),
  mounted() {
    //   this.currentPhysician = this.changer("");
  },
  computed: {
    physicians() {
      return this.$store.getters.PHYSICIAN;
    },
    physicianLookup() {
      return this.physicians.map(row => {
        let res = {};
        res.name = row.last_name + ", " + row.first_name;
        res.npi = row.npi;
        return res;
      });
    },
    currentUser() {
      return this.$store.getters.CURRENT_USER;
    },
    selectedEducation() {
      return this.physicianEducation.find(p => p.id === this.educationIndex);
    }
  },
  methods: {
    selectPhysician: async function(sel) {
      this.currentPhysician = Object.assign(
        {},
        this.physicians.find(p => p.npi === sel)
      );

      // fetch education records for npi
      httpService.get(
        `${Config.apiRoot}/physician/education/${this.currentPhysician.npi}`,
        (status, data) => {
          this.physicianEducation = data;

          this.showEducationDialog = false;
          this.showPhysicianProfile = true;
        }
      );
    },
    getEducationImage: function(id) {
      console.log("getEducationImage called");
      // fetch education blob, async wasn't working
      return new Promise((resolve, reject) => {
        httpService.get(
          `${Config.apiRoot}/physician/education/blob/${id}`,
          (status, data) => {
            if (status != 200) {
              reject("Problem fetching image");
            }
            resolve(data);
          }
        );
      });
      // httpService.get(
      //   `${Config.apiRoot}/physician/education/blob/${id}`,
      //   (status, data) => {
      //     console.log("data - ", data);
      //     return data;
      //   }
      // );
    },
    educationSelection: async function(type, auditID, blobID) {
      // if image (historical document), load differently
      if (type.toLowerCase() === "image") {
        //TODO get blob and showEducation
        this.getEducationImage(blobID).then(image => {
          // document.getElementById("fileViewerTitle").innerText =
          //   image[0].originalname;
          // let embeddedObj = document.getElementById("fileViewer");
          // embeddedObj.src = null;
          // embeddedObj.src = `data:application/pdf;base64,${Buffer.from(
          //   image[0].blob.data
          // ).toString("base64")}`;
          // this.$refs.fileViewerDialog.title = image[0].originalname;
          this.$refs.fileViewerDialog.show(
            image[0].originalname,
            `data:application/pdf;base64,${Buffer.from(
              image[0].blob.data
            ).toString("base64")}`
          );
          //this.fileViewerDialog = true;
        });
        // document.getElementById("fileViewerTitle").innerText =
        //   image.originalname;

        // document.getElementById(
        //   "fileViewer"
        // ).src = `data:application/pdf;base64,${Buffer.from(
        //   image[0].blob.data
        // ).toString("base64")}`;
        // this.fileViewerDialog = true;

        //
        //show
      } else {
        this.showEducation(auditID);
      }
    },
    showEducation: function(id) {
      this.educationIndex = id;
      //      this.selectedNpi; // WADE
      this.showEducationDialog = true;
    },
    showMessage: function(type, message) {
      this.$refs.customSnackbar.showSnackbar(message, type, 3000, false);
    },
    closeEducation: function() {
      this.showEducationDialog = false;
    }
  }
};
</script>

<style>
i:hover {
  cursor: pointer;
}
</style>
 