<template>
  <div>
    <v-container>
      <v-layout row wrap>
        <v-flex xs12 md12 class="pr-1">
          <v-card class="mb-2">
            <v-card-title
              class="headline font-weight-regular blue-grey white--text"
            >Audit History - {{ audits[0].provider_name }}</v-card-title>
            <v-card-text>
              <v-data-table
                :headers="tableHeaders"
                :items="audits"
                class="evelation-1"
                :pagination.sync="pagination"
              >
                <template slot="items" slot-scope="{ item }">
                  <tr
                    @click="auditPhysician(item.npi, item.provider_audit_id)"
                    style="cursor:pointer;"
                  >
                    <td>{{ item.status }}</td>
                    <td>{{ item.audit_date }}</td>
                    <td>{{ item.med_system }}</td>
                    <td>{{ item.billing_system }}</td>
                    <td>{{ item.audit_type }}</td>
                    <td>{{ item.in_progress }}</td>
                    <td>{{ item.complete }}</td>
                  </tr>
                </template>
              </v-data-table>
            </v-card-text>
          </v-card>
        </v-flex>
      </v-layout>
    </v-container>
  </div>
</template>

<script>
import Config from "../config";
import Router from "../router";
import httpService from "../httpService";

export default {
  name: "Main",
  components: {},
  data: () => ({
    audits: [],
    npi: "1326219569",
    tableHeaders: [
      { text: "Status", value: "status" },
      { text: "Audit Date", value: "audit_date" },
      { text: "Med System", value: "med_system" },
      { text: "Billing System", value: "billing_system" },
      { text: "Audit Type", value: "audit_type" },
      { text: "In Progress", value: "in_progress" },
      { text: "Completed", value: "complete" }
    ],
    pagination: {
      rowsPerPage: 10
    }
  }),
  async mounted() {
    console.log("mounted");
    this.getAudits();
  },
  computed: {
    auditors() {
      return this.$store.getters.ALL_USERS;
    },
    currentUser() {
      return this.$store.getters.CURRENT_USER;
    }
  },
  methods: {
    getAudits() {
      httpService.get(
        `${Config.apiRoot}/audit/physician-audits/${this.npi}`,
        (status, data) => {
          this.audits = data.message;

          //preset 2 fields for sorting
          for (let i = 0; i < this.audits.length; i++) {
            this.audits[i].status = this.getStatus(this.audits[i]);
            this.audits[i].in_progress = this.calcIncomplete(this.audits[i]);
          }
        }
      );
    },
    calcIncomplete(provider) {
      // incomplete returns 1 if there are no joined rows, check then adjust
      if (provider.count_of_linked_rows === 0) {
        return 0;
      }
      return provider.incomplete;
    },
    getStatus(provider) {
      if (provider.audit_completed_date) {
        return "Completed";
      }
      return "In Progress";
    },
    auditPhysician(npi, provider_audit_id) {
      // user selected a record to work on
      Router.push({
        name: "audit-provider",
        params: { npi: npi, provider_audit_id: provider_audit_id }
      });
    },
    auditPatient(id) {
      // user selected a record to work on
      Router.push({ name: "audit-record", params: { audit_id: id } });
    }
  }
};
</script>

<style>
.v-datatable th,
.v-datatable td {
  padding-left: 5px !important;
  padding-right: 5px !important;
}
</style>